/* Components */

/**
 * Featured Yachts
 */

.our-yachts{
  text-align: center;
  h2 {
     color: #000000;
     font-family: $futura;
     font-size: 36px;
     font-weight: 300;
     letter-spacing: 4px;
     line-height: 44px;
     text-transform: uppercase;
     & strong{
       font-weight: 600;
     }
  }
}

.feat-yachts{
  & .card-img{
    border-radius: 0px;
  }
  & .card-img-overlay{
    height: 100%;
    background-color: $color-transparent;
    transition: background-color 0.6s ease;
    & .h5{
      color: $color-white;
      font-family: $futura;
      font-size: 32px;
      font-weight: 300;
      letter-spacing: 4px;
      line-height: 44px;
      text-transform: uppercase;
      position: relative;
      opacity: 1;
      top: 90%;
      transition: opacity 0.4s ease;
    }
  }
  & .overlayHover{
    background-color: $transparent;
    padding: 15px;
    opacity: 0;
    transition: all 1s ease;
    &Card{
      min-height: 100%;
      background: $transparent;
      border: 1px solid $color-white;
      border-radius: 0px;
      padding: 30px;
      padding-top: 15%;
      text-align: center;
      & .overlayHoverCardTitle{
        color: $color-white;
        font-family: $futura;
        font-size: 32px;
        font-weight: 300;
        letter-spacing: 4px;
        line-height: 44px;
        text-transform: uppercase;
        position: relative;
        opacity: 1;
        top: 40%;
        transition: opacity 0.4s ease;
      }
      & button{
        max-width: 120px;
        text-align: center;
      }
    }
    &:hover{
      opacity: 1;
      background-color: rgba(0,0,0,0.45);
      &Card{
      }
    }
  }
  &:hover{
    & .card-img-overlay{
      & .h5{
        opacity: 0;
      }
    }
  }
}

.featured-yacht{
  border-radius: 0px;
  max-width: 100%;
  height: auto;
}

.even-yacht{
  max-width: 100%;
  height: auto;
  border-radius: 0px;
}

.odd-yacht{
  max-width: 100%;
  height: auto;
  border-radius: 0px;
}

.yachtSpacer{
  margin-top: auto;
  margin-bottom: auto;
  height: 5px;
  background-color: #f1f1f1;
  border-top: 0px;
}

/**
 * Yacht Charter
 */

 .yacht-charter{
   background-image: url('./images/tmp/charterBG.png');
   background-position: center;
   max-height: 433px;
   h2 {
      color: #000000;
      font-family: $futura;
      font-size: 36px;
      font-weight: 300;
      letter-spacing: 4px;
      line-height: 44px;
      text-transform: uppercase;
      & strong{
        font-weight: 600;
      }
   }
   p{
      color: #000000;
      font-family: $source;
      font-size: 20px;
      font-weight: 300;
      line-height: 28px;
   }
   .quoteCard{
     box-shadow: 0 2px 40px rgba(0, 0, 0, 0.09);
     padding: 15px;
     border-radius: 0px;
     & .quoteCard_inner{
      border: 1px black solid;
      padding: 30px;
      padding-left: 90px;
      font-family: $source;
      font-size: 24px;
      font-weight: 300;
      &::before{
        content: "\f10d";
        font-family: $fa;
        position: absolute;
        left: 46px;
        font-size: 30px;
        color: $color-gold;
      }
     }
   }
 }

 .row-paddingTopTen{
   padding-top: 10px;
 }

 .yacht-charter-hr{
   width: 253px;
   height: 30px;
   background-color: $transparent;
   border: 0px solid white;
   padding-top: 0px;
   &-inner{
     margin-top: 0px;
     margin-bottom: 0px;
     border-width: 5px;
     background-color: $color-gold;
   }
 }

