
/* Footer */

.footer{
  height: 116px;
  background-color: $color-gold;
}

.footer_item{
  color: $color-white;
  font-family: $source;
  font-size: 23px;
  font-weight: 400;
  line-height: 44px;
  &:hover{
    color: $color-grey;
    text-decoration: none;
    opacity: 0.75;
  }
}

.soc-footer{
  &::before{
    font-size: 26px;
  }
}

.telephone{
  &::before{
    margin-right: 20px;
    content: "\f095";
    font-family: $fa;
  }
}

.email{
  &::before{
    margin-right: 20px;
    content: "\f003";
    font-family: $fa;
  }
}
