/* Variables! */

/* Colours: */

$color-gold: #a99b6c;
$color-gold--dark: #7C724F;
$color-gold--fifty: rgba(169,155,108,0.5);


$color-white: #ffffff;
$white: $color-white;


$color-grey--light: #C9C9C9;
$color-grey: #f2f2f2;
$color-grey--dark: #494949;


$color-black--fourty: rgba(255,255,255,0.4);
$color-black--seventyfive: rgba(255,255,255,0.75);

$color-transparent: rgba(0,0,0,0);
$transparent: $color-transparent;
$trans: $color-transparent;


/* Fontstacks */

$source: 'Source Sans Pro', sans-serif;
$futura: 'futura-pt', sans-serif;
$fa: 'FontAwesome';
