/* MENU */

.menuItem:first-child{
  border-left: 1px solid $color-grey--light;
}
.menuItem{
  border-right: 1px solid $color-grey--light;
  padding-left: 35px;
  padding-right: 35px;
  display: flex;
  align-items: center;
}

.ss-menuLink{
  max-width: 180px;
  text-align: center;
  color: $color-grey--dark;
  font-family: $futura;
  font-size: 18px;
  font-weight: 300;
  letter-spacing: 2px;
  text-transform: uppercase;
  transition: color 0.5s ease;
  & strong{
    font-weight: 600;
  }
  &:hover{
    color: $color-gold;
  }
}
