/* Buttons */

.btn-primary {
  height: 45px;
  width: auto;
  min-width: 164px;
  font-family: $futura;
  background-color: $color-gold;
  border-color: $color-gold;
  color: $color-white;
  border-radius: 0px;
  font-size: 12px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 1.71px;
  &:hover {
    background-color: $color-gold--dark;
    border-color: $color-gold--dark;
  }

  &:hover {
    background-color: $color-gold--dark;
    border-color: $color-gold--dark;
  }
  &:disabled {
    cursor: no-drop;
    background-color: $color-gold--dark;
    border-color: $color-gold--dark;
  }
}

.btn-alt{
  height: 45px;
  width: auto;
  min-width: 164px;
  font-family: $futura;
  background-color: #6c757d;
  border-color: #6c757d;
  color: $color-white;
  border-radius: 0px;
  font-size: 12px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 1.71px;
  &:hover{

  }
  &:disabled{
    cursor: no-drop;
    background-color: $color-gold--dark;
    border-color: $color-gold--dark;
    text-decoration: line-through;
  }
}

.btn-nostyle{
  height: 44px;
  color: #5f5f5f;
  font-family: $source;
  font-size: 16px;
  font-weight: 400;
  &:first-child{
    margin-right: 30px;
  }
  &:hover{
    color: $color-gold;
  }
}

.btn-reverse{
  height: 45px;
  width: auto;
  min-width: 164px;
  font-family: $futura;
  background-color: $color-transparent;
  border-color: $color-gold;
  color: $color-gold;
  border-radius: 0px;
  font-size: 12px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 1.71px;
  &:hover{
    background-color: $color-gold;
    color: $color-white;
  }
  &:disabled{
    cursor: no-drop;
    background-color: $color-gold--fifty;
    color: $color-gold;
  }
}

.btn-reverse-white{
  height: 45px;
  width: auto;
  min-width: 164px;
  font-family: $futura;
  background-color: $color-transparent;
  border-color: $color-white;
  color: $color-white;
  border-width: 2px;
  border-radius: 0px;
  font-size: 12px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 1.71px;
  &:hover{
    background-color: $color-black--fourty;
    color: $color-white;
  }
}


.btn-s{
  height: 31px;
}

.btn-m{
  height: 45px;
}

.btn-l{
  height: 60px;
  min-width: 266px;
  font-size: 18px;
  font-weight: 600;
  letter-spacing: 2.57px;
  padding-left: 25px;
  padding-right: 25px;
}


.btn-fb{
  width: 18px;
  height: 18px;
  &::before{
    content: "\f230";
    font-family: $fa;
  }
}

.btn-tw{
  width: 18px;
  height: 18px;
  &::before{
    content: "\f081";
    font-family: $fa;
  }
}


.btn-in{
  width: 18px;
  height: 18px;
  &::before{
    content: "\f16d";
    font-family: $fa;
  }
}


.soc-primary{
  color: $color-gold;
  &:hover{
    color: $color-gold--dark;
  }
}

.soc-white{
  color: $color-white;
  &:hover{
    color: $color-black--seventyfive;
  }
}
