/* Globals */

/* card image overlay */

.card-img-overlay{
  text-align: center;
}

.heroOverlay{
  padding-top: 28%;
  &_title{
    color: $color-white;
    font-family: $futura;
    font-weight: 600;
    font-style: normal;
    font-size: 47px;
    letter-spacing: 4px;
    line-height: 53px;
    text-transform: uppercase;
  }
  &_text{
    color: $color-white;
    font-family: $futura;
    font-weight: 300;
    font-style: normal;
    font-size: 33px;
    line-height: 53px;
  }
}

.sliderOverlay{
  padding-top: 21%;
  &_title{
    color: $color-white;
    font-family: $futura;
    font-weight: 600;
    font-style: normal;
    font-size: 47px;
    letter-spacing: 4px;
    line-height: 53px;
    text-transform: uppercase;
  }
  &_text{
    color: $color-white;
    font-family: $futura;
    font-weight: 100;
    font-style: normal;
    font-size: 33px;
    line-height: 53px;
  }
}

.row {
  padding-top: 30px;
  &:last-child{
    padding-bottom: 30px;
  }
}
